
/*      "//"   ' new Date */

export const parseDateToObject = dataString => {
  //    ,   
  let [day, month, year] = dataString.split('/');

  //      
  if (isNaN(day) || isNaN(month) || isNaN(year)) {
    throw new Error('  ');
  }

  //     
  day = parseInt(day);
  month = parseInt(month) - 1; // -1     new Date
  year = parseInt(year);

  return new Date(year, month, day);
};
